@media screen and (max-width: 1199px) {
  .mobal-menu {
    width: 250px;
    background: #e17992;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
    transform: translateX(100%);
  }
}

.mobal-menu .order-button {
  position: relative;
  top: 20px;
}

.mobal-menu.is-open {
  transform: translateX(0%);
}

.mobal-menu.is-open ~ .order-button--tablet {
  display: block;
  position: fixed;
  top: 60px;
  right: 32px;
}

