.reviews {
  text-align: center;
}

.slider {
  text-align: center;
  position: relative;
}

.slider__img {
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.reviews__bg {
  background-color: var(--background-color-reviews);
  max-width: 280px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px 50px 17px;
  display: inline-block;
  position: relative;
}

@media screen and (min-width: 768px) {
  .reviews__bg {
    max-width: 620px;
    padding: 20px 6px 36px;
  }
}

