  .reviews__bg {
    max-width: 968px;
    padding: 32px 74px 31px 72px;
  }
}

.reviews__thumb {
  background-image: url(".e1c954c9.png");
  background-repeat: no-repeat;
  margin-left: auto;
  margin-right: auto;
  padding: 13px 8px 8px 15px;
  display: inline-block;
  position: relative;
}

@media screen and (min-width: 768px) {
  .reviews__thumb {
    padding: 17px 40px 8px 44px;
  }
}

