.tagline {
        @media(max-width: 767px) {
                margin-top: 139px;
                font-family: 'Titan One';
                font-style: normal;
                font-weight: 400;
                font-size: 26px;
                line-height: 1.15;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: var(--primary-white-color);
            } 

        @media(min-width:768px) {
                margin-top: 20px;
                font-family: 'Titan One';
                font-style: normal;
                font-weight: 400;
                font-size: 22px;
                line-height: 1.13;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: var(--primary-white-color);
            }

        @media (min-width:1200px) {
                margin-top: 75px;
                margin-left: 0;
                font-family: 'Titan One';
                font-style: normal;
                font-weight: 400;
                font-size: 38px;
                line-height: 1.15;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: var(--primary-white-color);
            }
}

.girl{
    display: none;

    @media (min-width:768px) {
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;
    }
    
    @media (min-width:1200px) {
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;
        width: 304px;
        height: 241px;
    }
}

.milk{
    display: none;

    @media (min-width:768px) {
        display: block;
        position: absolute;
        left: 32px;
        bottom: 16px;
        width: 152px;
        height: 154px;
    }

    @media (min-width:1200px) {
        display: block;
        position: absolute;
        left: 32px;
        bottom: 60px;
        width: 213px;
        height: 216px;
    }
}

