.text-button{
    display: none;

    @media (min-width:768px) {
            display: block;
            position: absolute;
            left: 184px;
            bottom: 40px;
            width: 18px;
            height: 18px;
            background-color: transparent;
            border: transparent;
            border-radius: 50%;
            

         &::after{
             position: absolute;
             background: url('../images/hero.scss/arrow-right_18x18.svg');
             content: "";
             width: 18px;
             height: 18px;
             background-repeat: no-repeat;
             bottom: 0px;
             left: 0;
             z-index: -1;
        }  
             
    }

    @media (min-width:1200px) {
            width: 26px;
            height: 26px;
            left: 230px;
            bottom: 45px;

        &::after {
                position: absolute;
                background: url('../images/hero.scss/arrow-right_26x26.svg');
                content: "";
                width: 26px;
                height: 26px;
                background-repeat: no-repeat;
                bottom: 0px;
                left: 0;
                z-index: -1;
            }         
    }       
}

.flex-conteiner{
   display: none;

    @media (min-width:768px) {
            display: flex;
            flex-direction: column;
            position: absolute;
            font-family: 'DM Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 8px;
            line-height: 1.25;
            text-align: right;
            color: var(--primary-white-color);
        }

    @media (min-width:1200px) {
            display: flex;
            flex-direction: column;
            position: absolute;
            font-family: 'DM Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 1.28;
            text-align: right;
            color: var(--primary-white-color);
        }    
}

.upper{
    top: 94px;
    right: 32px;

    @media (min-width:1200px) {
            top: 156px;
            right: 25px;
            margin-bottom: 38px;
        }
        
        &::after {
            position: absolute;
            right: -8px;
            bottom: 30px;
            z-index: -1;
            content: "";
            width: 16px;
            height: 16px;
            background-color: var(--primary-white-color);
            border-radius: 50%;
    
        @media (min-width:1200px) {
                position: absolute;
                right: -8px;
                bottom: 50px;
                z-index: -1;
                content: "";
                width: 30px;
                height: 30px;
                background-color: var(--primary-white-color);
                border-radius: 50%;
            }
    
        }
}



.bottom{
    top: 151px;
    right: 32px;

    @media (min-width:1200px) {
            top: 247px;
            right: 25px;
    }

        &::after {
            position: absolute;
            right: -8px;
            bottom: 30px;
            z-index: -1;
            content: "";
            width: 16px;
            height: 16px;
            background-color: var(--primary-white-color);
            border-radius: 50%;
    
        @media (min-width:1200px) {
                position: absolute;
                right: -8px;
                bottom: 46px;
                z-index: -1;
                content: "";
                width: 30px;
                height: 30px;
                background-color: var(--primary-white-color);
                border-radius: 50%;
            }
        }
}


.upper__number,
.bottom__number{
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 1.14;
    text-align: right;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--primary-title-color);

@media (min-width:1200px) {
