    border-radius: 20px;
    border: transparent;

        @media (min-width:768px) {
            margin-left: 0;
            width: 62.4px;
            height: 24px;
        }

        @media (min-width:1200px) {
            margin-left: 0;
            width: 107px;
            height: 40px;
        }

    &__text--white {
            display: flex;
            align-items: center;
            text-align: center;
            justify-content: center;
            font-family: 'DM Sans, sans serif';
            font-style: normal;
            font-weight: 400;
